// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin button-variant($background, $border, $hover-background: adjust-color($background, $saturation: 2%, $lightness: -5%), $hover-border: adjust-color($background, $saturation: 2%, $lightness: -5%), $active-background: color-trans($background, 0.6), $active-border: color-trans($border, 0.6)) {
  color: color-yiq($background);
  @include gradient-bg($background);
  border-color: $border;
  @include box-shadow($btn-box-shadow);

  @include hover() {
    color: color-yiq($hover-background);
    @include gradient-bg($hover-background);
    border-color: $hover-border;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active,
  .show > &.dropdown-toggle {
    color: color-yiq($active-background);
    background-color: $active-background;
    @if $enable-gradients {
      background-image: none; // Remove the gradient for the pressed/active state
    }
    border-color: $active-border;
  }
}

@mixin button-outline-variant($color, $active-background: color-trans(adjust-color($color, $saturation: 2%, $lightness: -5%), 0.05, #ffffff), $active-border: color-trans($color, 0.60)) {
  color: $color;
  border-color: $color;

  @include hover() {
    background-color: $active-background;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active,
  .show > &.dropdown-toggle {
    color: $active-border;
    background-color: color-trans($active-background, 0.60);
    border-color: $active-border;
  }
}

// Button sizes
@mixin button-size($height, $width, $font-size, $line-height, $border-radius) {
  height: $height;
  width: $width;
  @include font-size($font-size);
  line-height: $line-height;
  // Manually declare to provide an override to the browser default
  @include border-radius($border-radius, 0);
}
