// stylelint-disable declaration-no-important

// Common values
@each $position in $positions {
  .position-#{$position} { position: $position !important; }
}

// Shorthand

.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: $zindex-fixed;
}

.fixed-bottom {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $zindex-fixed;
}

@each $breakpoint in map-keys($grid-breakpoints) {
  @each $size, $length in $spacers {
    @include media-breakpoint-up($breakpoint) {
      .top#{breakpoint-infix($breakpoint, $grid-breakpoints)}-#{$size}{
        top: $length !important;
        transform: none !important;
      }
  
      .left#{breakpoint-infix($breakpoint, $grid-breakpoints)}-#{$size}{
        left: $length !important;
        transform: none !important;
      }

      .right#{breakpoint-infix($breakpoint, $grid-breakpoints)}-#{$size}{
        right: $length !important;
        transform: none !important;
      }

      .bottom#{breakpoint-infix($breakpoint, $grid-breakpoints)}-#{$size}{
        bottom: $length !important;
        transform: none !important;
      }
    }
  }

  @include media-breakpoint-up($breakpoint) {
    .center#{breakpoint-infix($breakpoint, $grid-breakpoints)}-x {
      left: 50% !important;
      transform: translateX(-50%) !important;
    }
    
    .center#{breakpoint-infix($breakpoint, $grid-breakpoints)}-y {
      top: 50% !important;
      transform: translateY(-50%) !important;
    }
    
    .center#{breakpoint-infix($breakpoint, $grid-breakpoints)}-xy {
      top: 50% !important;
      left: 50% !important;
      transform: translate(-50%, -50%) !important;
    }
  }
}

.sticky-top {
  @supports (position: sticky) {
    position: sticky;
    top: 0;
    z-index: $zindex-sticky;
  }
}
